@import url('https://fonts.googleapis.com/css?family=Rajdhani:300&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

/* Glassmorphism Background Variables */
:root {
  --blur: 20px;
  --shadow-opacity: 0.30;
  --bg-color-1: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --bg-color-2: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  --bg-color-3: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  --bg-color-4: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
  --bg-color-5: linear-gradient(135deg, #fa709a 0%, #fee140 100%);
  --bg-color-6: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
  --bg-color-7: linear-gradient(135deg, #ff9a9e 0%, #fecfef 100%);
  --bg-color-8: linear-gradient(135deg, #ffecd2 0%, #fcb69f 100%);
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

/* Dynamic Background Container */
#root {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

#root::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--bg-color-1);
  z-index: -2;
  animation: backgroundShift 20s ease-in-out infinite;
}

#root::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(var(--blur));
  -webkit-backdrop-filter: blur(var(--blur));
  z-index: -1;
}

@keyframes backgroundShift {
  0% { background: var(--bg-color-1); }
  12.5% { background: var(--bg-color-2); }
  25% { background: var(--bg-color-3); }
  37.5% { background: var(--bg-color-4); }
  50% { background: var(--bg-color-5); }
  62.5% { background: var(--bg-color-6); }
  75% { background: var(--bg-color-7); }
  87.5% { background: var(--bg-color-8); }
  100% { background: var(--bg-color-1); }
}

/* Glass Effect for Main Content */
.glass-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(var(--blur));
  -webkit-backdrop-filter: blur(var(--blur));
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, var(--shadow-opacity));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #374151;
}

::-webkit-scrollbar-thumb {
  background: #6b7280;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}

/* Monaco Editor custom styles */
.monaco-editor {
  background-color: #1f2937 !important;
}

.monaco-editor .margin {
  background-color: #1f2937 !important;
}

.monaco-editor {
  border-radius: 8px;
}